"use client";
import React, { useCallback } from "react";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import TextInput from "../../shared/TextInput";
import useForm from "@/app/_hook/useForm";
import Logo from "../../Logo/Logo";
import { useRouter } from "next/navigation";
import CustmoizedButton from "../../shared/CustmoizedButton";
import { Input } from "@/components/ui/input";

const Login = () => {
  const router = useRouter();
  const initialValues = {
    email: "",
    password: "",
  };

  const validate = useCallback((values) => {
    const errors = {};
    if (!values.email) errors.email = "البريد الالكتروني مطلـوب";
    if (!values.password) errors.password = "كلمة المرور مطلوبة";
    return errors;
  }, []);

  const { formData, errors, isValid, handleChange } = useForm(
    initialValues,
    validate
  );

  const handleSubmit = useCallback(
    (e) => {
      e.preventDefault();
      if (isValid) {
        router.replace("/home");
      }
    },
    [isValid, router]
  );

  return (
    <form className="w-72 p-3 bg-secondary rounded-sm text-white">
      <div className="form-header mb-4">تسجيل دخول</div>
      <div className="inputs">
        <div className="input">
          <label htmlFor="username"></label>
          <input type="text" required name="user-name" className="boder-none"/>
        </div>
      </div>
    </form>
  );
};

export default React.memo(Login);
